import React, { Component } from "react";

class Roster_hover_zabrome extends Component {
  constructor() {
    super();
    this.state = { isHover: false };
  }

  reveal = () => {
    this.setState({ isHover: !this.state.isHover });
  };

  render() {
    const { isHover } = this.state;
    return (
      <div
        className="card mask"
        onMouseEnter={this.reveal}
        onMouseLeave={this.reveal}
      >
        {isHover ? (
          <div className="flex" style={{ height: "100%" }}>
            <div className="flex">
              <div
                className="above"
                style={{
                  color: "rgb(211,224,224)",
                  border: "1px solid rgb(211,224,224)"
                }}
              >
                S5 Silver
              </div>
              <div className="above">S7-9 Gold</div>
            </div>
            <div className="bg-letter name">Zabrome</div>
            <div className="flex">
              <div className="under white">LoL @ Zabrome</div>
              <div className="under white">Tier IV Clash</div>
              <div className="under white">Discord @ Zabrome</div>
            </div>
          </div>
        ) : (
          <div>Zabrome</div>
        )}
      </div>
    );
  }
}

export { Roster_hover_zabrome };

